.auth-scheme-section {
  background-color: #fafafa;
}
.auth-scheme-section .del-res-icon {
  display: none;
  margin-left: 6px;
}
.auth-scheme-section .dep-item {
  margin-bottom: 20px;
  cursor: pointer;
}
.auth-scheme-section .dep-item:hover .del-res-icon {
  display: inline-block;
}
.auth-scheme-section .dialog-title {
  font-size: 24px;
  font-weight: bold;
  text-align: left;
}
.auth-scheme-section .resource-name {
  display: inline-block;
  max-width: 90%;
}
.auth-scheme-section .resource-name .el-icon-edit {
  color: #999999;
  margin-left: 6px;
}
.auth-scheme-section .select-box {
  vertical-align: top;
}
.auth-scheme-section .dep-list-wrap {
  width: 430px;
  background-color: white;
  float: left;
  position: relative;
}
.auth-scheme-section .dep-list-wrap .dep-list-inner {
  padding: 50px 12px 20px 50px;
}
.auth-scheme-section .el-icon-question {
  color: #f5e2c6;
  background-color: #fcab68;
  border-radius: 50%;
  vertical-align: middle;
}
.auth-scheme-section .dot {
  padding: 0 13px;
}
.auth-scheme-section .policy-input-title {
  margin-bottom: 15px;
  font-size: 16px;
}
.auth-scheme-section .policy-input-title .el-icon-question {
  margin-right: 9px;
}
.auth-scheme-section .line-arrow {
  display: none;
  height: 1px;
  background-color: black;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1;
}
.auth-scheme-section .line-arrow .circle {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1px solid black;
  position: absolute;
  right: -12px;
  top: -5px;
}
.auth-scheme-section .active .line-arrow {
  display: inline-block;
}
.auth-scheme-section .add-dep-resource-btn {
  color: #CCCCCC;
  margin-left: 30px;
}
.auth-scheme-section .dialog-footer .del-btn {
  background-color: #CCCCCC;
  border: 1px solid #666666;
  color: white;
  float: left;
}
.auth-scheme-section .dialog-footer .ft-btn {
  float: right;
}
.auth-scheme-section.scheme-status-1 .add-dep-resource-btn {
  display: none;
}
.auth-scheme-section.scheme-status-1 .dep-list-inner .resource-name .el-icon-edit {
  display: none;
}
.auth-scheme-section.scheme-status-4 .del-res-icon,
.auth-scheme-section.scheme-status-4 .add-dep-resource-btn {
  display: none !important;
}
.auth-scheme-section.scheme-status-4 .policy-input-wrap {
  position: relative;
}
.auth-scheme-section.scheme-status-4 .policy-input-wrap:before {
  display: block;
  content: ' ';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
  bottom: 0;
  z-index: 2;
  border-radius: 4px;
}
